.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    /* height: 20vmin; */
    height: 50px;
  }

  @-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

  @keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

body {
    background: #282C34;
    color: #61DAFB;
    font-family: 'segoe ui','Merriweather Sans', sans-serif;
}

p {
    font-size: 18px;
    line-height: 1.5;
}

div {
    text-align: center;
}

.add-project {
    font-size: 36px;
    font-family: 'segoe ui','Merriweather Sans', sans-serif;
    padding: 25px 25px 0 25px;
}

.description {
    border-left: 7px solid #fff;
    padding-left: 20px;
    width: 500px;
    margin: 0 auto 40px;
    text-align: justify;
}

input {
    border: none;
    padding: 14px;
    font-size: 16px;
    margin-bottom: 20px;
}

input:focus {
    outline: 0;
}

select {
    padding: 10px;
    font-size: 16px;
    margin-top: 10px;
}

.submit {
    border-radius: 5px;
    background: #61DAFB;
    color: #282C34;
    font-weight: bold;
    margin: 30px 0;
    text-transform: uppercase;
    -webkit-appearance: none;
}

.latest-projects {
    text-decoration: underline;
    font-size: 25px;
    font-family: 'Merriweather Sans', sans-serif;
}

li {
    display: flex;
    justify-content: center;
    text-align: left;
}

button {
    font-size: 22px;
    text-decoration: none;
    margin-left: 15px;
    margin-top: -3px;
    padding: 0px 10px;
    background: #61DAFB;
    color: #282C34;
    border-radius: 5px;
    border: none;
}

hr {
    width: 100px;
    border: 1px solid #ffffff;
    margin: 30px auto;
}

Todos {
    margin-bottom: 20px;
}

@media screen and (max-width: 700px) {
    .description {
        border-left: none;
        padding-left: 0;
        width: 300px;
    }

    .add-project {
        font-size: 34px;
        font-family: 'Merriweather Sans', sans-serif;
        padding: 15px 25px 0 25px;
    }

    .labels {
        display: inline-block;
        font-size: 25px;
        font-weight: bold;
        margin-bottom: 20px;
        text-decoration: underline;
    }

    select {
        margin-top: 0px;
    }
}

